<template>
  <div id="bottom-popup">
    <div class="bottom-popup">
      <div class="popup-item" v-for="item in itemList" v-text="item.text" @click="selItem(item)"></div>
      <div class="popup-btn" v-text="btnText" @click="selItem('cancel')"></div>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      btnText: {
        type: String,
        default: '取消'
      },
      itemList: {
        type: Array,
        /*default: [
          {
            text: '存入礼物包',
            id: 1
          },
          {
            text: '实物发货',
            id: 0
          }
        ]*/
      }
    },
    data () {
      return {
        list: ''
      }
    },
    watch: {},
    methods: {
      selItem(item){
//        console.log(item)
        this.$emit('selItem',item);
      },
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  /* 底部弹框 */
  #bottom-popup{
    .bottom-popup{
      background: #d2d2d3;
      width: 100%;
      position: fixed;
      bottom: 0;
      left: 0;
      color: #221e1f;
      font-size: 0.48rem;
      line-height: 1.31rem;
      text-align: center;
    }
    .bottom-popup .popup-item,
    .bottom-popup .popup-btn
    {
      width: 100%;
      height: 1.31rem;
      background-color: #ffffff;
    }
    .bottom-popup .popup-item{
      margin-bottom: 1px
    }
    .bottom-popup .popup-btn{
      margin-top: 0.13rem;
    }
  }
</style>



