<template>
	<div id="search_header">
		<div class="header-bar">
			<div class="title" v-if="!hasBack">礼物飞</div>
			
			<div v-if="hasBack">
				<img class="lt-arrow" src="../../assets/arrow.png" @click="$router.go(-1)"/>
			</div>
			<div class="search" @click="linkTo('Search')">
				<i class="search-icon"></i>
				<span v-if="!isSearch" class="search-text">选份礼物送给Ta</span>
				<input v-if="isSearch" class="search-key" type="text" placeholder="选份礼物送给Ta" v-model="searchKey" @input="getSearch" @keyup.enter="search" ref="searchInput">
				<!--<input class="search-key" type="text" placeholder="选份礼物送给Ta" disable>-->
				<i class="clear-icon" v-show="isShow" @click="clearSearch"></i>
			</div>
			<slot name="icon"></slot>
			<!--<router-link :to="{ name : 'Cart'}">
				<div class="cart">
					<img class="cart-icon" src="../../assets/cart-icon.png">
					<span class="num-icon">2</span>
				</div>
			</router-link>-->
		</div>
	</div>
</template>

<script>
	export default {
		props:{
			hasBack:{
				type:Boolean,
				default:true
			}
			
		},
		data(){
			return{
				isShow:false,
				searchKey:'',
				isSearch:false,
			}
		},
		created(){
			console.log(this.$route)
			if(this.$route.name != 'Search'){
				this.isSearch = false
			}else{
				this.isSearch = true
			}
		},
		methods: {
			getSearch(){
				let length = this.$refs.searchInput.value.length
				if(length>0){
					this.isShow = true
				}else{
					this.isShow = false
				}
			},
			search(){
				console.log('回车')				
			},
			clearSearch(){
				this.searchKey = '';
				this.isShow = false;
			},
		}
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";

	#search_header{
		height: 1.17rem;
		.header-bar{
			display: flex;
			align-items: center;
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			z-index: 100;
			height: 1.17rem;
			padding: 0 0.4rem;
			@include bg-color;
			.lt-arrow{
				/*@include lt-arrow;*/
				width: 0.32rem;
    		height: 0.59rem;
			}
			.title{
				font-size: 0.4rem;
				color: #fff;
				min-width: 1.2rem;
			}
			.cart-icon{
				width: 0.64rem;
				height: 0.59rem;
				margin-left: 0.4rem;
			}
			.cart{
				position: relative;
				.num-icon{
					position: absolute;
					font-size: 0.32rem;
					right: -0.2rem;
   				top: -0.08rem;
					display: inline-block;
					min-width: 0.4rem;
					height: 0.4rem;
					border-radius: 50%;
					color: #fff;
					background: #fe4436;
					text-align: center;
				}
			}
			.share{
				width:0.59rem;
			  height:0.59rem;
			  margin-left: 0.4rem;
				.share-icon{
					width:0.59rem;
					height:0.59rem;
				}
			}
			.search{
				position: relative;
				/*width: 7.44rem;*/
				flex :1;
				height: 0.8rem;
				line-height: 0.8rem;
				margin-left: 0.4rem;
				border-radius: 0.4rem;
				background: rgb(255, 255, 255);
				.search-icon{
					display: inline-block;
					position: absolute;
					width: 0.4rem;
					height: 0.4rem;
					background-image: url(../../assets/search-icon.png);
					background-repeat: no-repeat;
					background-size: 0.4rem;
					left: 0.4rem;
					top: 50%;
					transform: translateY(-50%);
				}
				.search-text{
					position: absolute;
					left: 1.07rem;
					top: 50%;
					transform: translateY(-50%);
					font-size: 0.4rem;
					/*padding:0 1.07rem;*/
					color: #666;
				}
				.search-key{
					font-size: 0.4rem;
					width: 100%;
					padding:0 1.07rem;
					height: 0.8rem;
					line-height: 0.8rem;

				}
				.clear-icon{
					display: inline-block;
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: 0.27rem;
					width: 0.4rem;
					height: 0.4rem;
					background-size: contain;
					background-image: url(../../assets/clear-icon.png);
					background-repeat: no-repeat;
					
				}
			}
			
		}
	}
</style>


