<template>
  <div class="link-cell" @click="emitClick" v-if="type == 'cell'">
    <span>{{title}}</span>
    <span>{{value}}</span>
  </div>
  
  <div class="link-cell no-arrow" v-else-if="type == 'count'">
    <span>{{title}}</span>
    <div class="controller">
      <span class="control-sub control"
            :class="{'c-ccc': num == 0}"
            @click="count(-1)"
      >-</span>

      <input class="control-input" type="num" name="" :value="num" disabled>

      <span class="control-add control"
            :class="{'c-ccc': max?num >= max: false}"
            @click="count(1)"
      >+</span>
    </div>
  </div>

  <div class="link-cell no-arrow" v-else-if="type == 'input'">
    <span>{{title}}</span>
    <input type="text" class="text-input" @input="updateValue($event.target.value)" v-bind:value="value" :placeholder="placeholder">
  </div>
  <div class="link-cell no-arrow" v-else-if="type == 'number'">
    <span>{{title}}</span>
    <input type="number" class="text-input" @input="updateValue($event.target.value)" v-bind:value="value" :placeholder="placeholder">
  </div>
  <div class="link-cell no-arrow" v-else-if="type == 'password'">
    <span>{{title}}</span>
    <input type="password" class="text-input" @input="updateValue($event.target.value)" v-bind:value="value" :placeholder="placeholder">
  </div>
</template>

<script>
  export default {
    props: {
      title: {
        type: String,
        defalut: '暂无',
        required: true
      },

      value: {
        type: String
      },

      type: {
        type: String,
        default: 'cell' 
      },

      //input传进来的value值
      num: {
        type: Number
      },

      //input的最大值
      max: {
        type: Number
      },

      placeholder: {
        type: String,
        default: '请输入'
      }
    },

    methods: {
      emitClick() {
        this.$emit('click');
      },

      count(type) {
        if(type == -1 && this.num <= 0) {
          console.log('不能少于0')
          return false;
        }
        if(this.max && this.num >= this.max && type == 1) {
          console.log('不能大于最大值')
          return false;
        }
        this.$emit('change', type);
      },

      updateValue(val) {
        this.$emit('input', val)
      }
    }
  }
</script>


<style lang="scss">
  //link-cell
  .link-cell {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 0.8rem 0 0.4rem;
    height: 1.2rem;
    line-height: 1.2rem;
    background: #fff;
    font-size: 0.427rem;
    color: #333;

    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0.4rem;
      width: 0.173rem;
      height: 0.307rem;
      transform: translateY(-50%);
      background-image: url('../../assets/arrow_gray.png');
      background-size: 100%;
    }
  }


  .link-cell+ .link-cell {
    border-top: 1px solid #eee;
  }

  .no-arrow {
    padding-right: 0.4rem;

    &:after {
      content: none;
    }
  }

  .text-input {
    border: none;
    outline: none;
    text-align: right;
  }
	.text-input::-webkit-input-placeholder { /* WebKit browsers */ 
		font-size:0.37rem;
	} 
  .controller {
    margin-top: 0.186rem;
    color: #333;
    font-size: 0;

    .control {
      display: inline-block;
      width: 0.8rem;
      height: 0.8rem;
      line-height: 0.8rem;
      text-align: center;
      font-size: 0.67rem;
      border: 1px solid rgb(204, 204, 204);
      box-sizing: content-box;
    }
    .c-ccc{
      color: #ccc;
    }
    .control-sub {
      border-right: 0;
    }
    .control-add {
      border-left: 0;
    }
    .control-input {
      font-size: 0.53rem;
      width: 1.6rem;
      height: 0.8rem;
      line-height: 0.8rem;
      vertical-align: top;
      text-align: center;
      border: 1px solid rgb(204, 204, 204);
      box-sizing: content-box;
    }
  }
</style>




